html,
body,
div,
input,
textarea,
video,
em,
table,
tr,
td,
th,
label,
a,
p,
span,
ul,
li,
h1,
h2,
h3,
h4,
section,
nav,
img,
button,
input {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 100%;
  color: #384650;
  vertical-align: baseline;
}
body {
  background: #f8f9fa;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
img,
a,
button {
  border: 0 none;
  outline: 0 none;
}
table tbody tr:hover {
  background: #fffef4;
  transition: 0.3s;
}
a {
  text-decoration: none;
}
a.link,
a.ctx {
  color: #0040d2;
}
a.link:hover,
a.ctx:hover {
  color: #ec5200;
  transition: 0.3s;
}
b {
  font-weight: normal;
}
em {
  font-style: normal;
}
input {
  outline: none;
}
textarea {
  outline: none;
  resize: none;
}
textarea:focus {
  border: 1px solid #ec5200;
}
button {
  -webkit-appearance: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
input::-webkit-input-placeholder {
  color: #faf4ff;
}
input:focus:not(.side-bar-search)::-webkit-input-placeholder {
  color: #fff;
}
.clearfix:after {
  clear: both;
  display: block;
  font-size: 0;
  height: 0;
  visibility: hidden;
  content: "";
}
a {
  color: #0040d2;
}
a:visited {
  /* #653096? */
}
a:hover {
  color: #ec5200;
  transition: 0.3s;
