/* 遮层 */
.lock {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 9998;
  opacity: 0.3;
  filter: alpha(opacity=30);
}
.blur {
  -webkit-filter: blur(2px);
  filter: blur(2px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=10, MakeShadow=false);
}
.ovy {
  overflow-y: hidden;
}
/*加载线*/
.loading-line {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  width: 0;
  height: 2px;
  background: #ec5200;
}
/*线条加载动画*/
.lineAnimate {
  width: 0;
  height: 2px;
  position: absolute;
  bottom: 0px;
  left: 0;
  display: none;
  opacity: 0;
  filter: aplha(opacity=0);
  background: #ec5200;
}
